  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(90deg);
  width: 2em; }

.spinner, .spinner div {
  transform-origin: 50% 50%; }

.spinner div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; }

.spinner:before,
.spinner:after,
.spinner div:before,
.spinner div:after {
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  content: "";
  background: #fff;
  border-radius: 100%;
  width: 0.5em;
  height: 0.5em;
  position: absolute; }

.spinner:before,
.spinner div:before {
  animation-name: orbBounceBefore;
  top: -0.25em;
  left: -0.25em; }

.spinner:after,
.spinner div:after {
  animation-name: orbBounceAfter;
  top: -0.25em;
  right: -0.25em; }

.spinner-a {
